import React, { useState, useEffect } from 'react';
import "../styles/index.css";
import * as d3 from 'd3';
import Layout from '../components/Layout';
import Sidebar from '../components/Sidebar';
import { graphql } from 'gatsby';
import { withPrefix } from 'gatsby';
import StateMap from '../components/StateMap';
import logo from '../images/NACA.png';
import { navigate } from 'gatsby';


const states = [
  "All States", "Abia", "Adamawa", "Akwa Ibom", "Anambra", "Bauchi", "Bayelsa", "Benue",
  "Borno", "Cross River", "Delta", "Ebonyi", "Edo", "Ekiti", "Enugu",
  "Federal Capital Territory", "Gombe", "Imo", "Jigawa", "Kaduna", "Kano",
  "Katsina", "Kebbi", "Kogi", "Kwara", "Lagos", "Nasarawa", "Niger", "Ogun",
  "Ondo", "Osun", "Oyo", "Plateau", "Rivers", "Sokoto", "Taraba", "Yobe", "Zamfara"
];

const services = [
  "All Services",
  "Condom Distribtion",
  "Family Life and HIV/AIDS Education (FLHE)/Sexuality Education",
  "Gender and Human Rights (GHR)",
  "Harm Reduction: Medication-Assisted Treatment (MAT)",
  "Harm Reduction: Needle and Syringe Exchange",
  "HIV Self-Testing (HIVST)",
  "HIV Testing Services (HTS)",
  "Mental Health Services",
  "Pre-Exposure Prophylaxis (PrEP)",
  "Social and Behaviour Change Communication (SBCC)",
  "STI Screening and Treatment",
  "Technical Assistance (TA)"
];

const populations = [
  "All Populations",
  "Adolescent Girls and Young Women (AGYW) in school",
  "Adolescent Girls and Young Women (AGYW) out of school",
  "Adolescent Boys and Young Men (ABYM) in school",
  "Adolescent Boys and Young Men (ABYM) out of school",
  "Children living with HIV",
  "General population",
  "HIV-exposed infants",
  "Internally Displaced People (IDP)",
  "Key Populations - Female Sex Workers (FSW)",
  "Key Populations - Men who have Sex with Men (MSM)",
  "Key Populations - People in enclosed settings",
  "Key Populations - People Who Inject Drugs (PWID)",
  "Key Populations - Transgender",
  "Orphans and Vulnerable Children (OVC)",
  "Parents",
  "People living with disabilities",
  "People Living with HIV (PLHIV)",
  "Serodiscordant couples",
  "Teachers",
  "Faith Leaders",
  "Fishermen",
  "Pastoralists",
  "Transport Workers"
];

const locations = [
  "All Locations",
  "Community",
  "Facility"
];


export const query = graphql`
query($state: String!) {
  allServiceProvidersCsv(filter: { state: { eq: $state } }) {
    nodes {
      state
      lga_name
    }
  }
}
`;

const StateTemplate = ({ data }) => {
  const stateName = data.allServiceProvidersCsv.nodes[0]?.state;
  const [currentHeading, setCurrentHeading] = useState(stateName || "N/A");
  console.log("State Name in StateTemplate:", stateName);
  const [selectedService, setSelectedService] = React.useState(services[0]);
  const [selectedPopulation, setSelectedPopulation] = React.useState(populations[0]);
  const [selectedLocation, setSelectedLocation] = React.useState(locations[0]);
  const [selectedLga, setSelectedLga] = React.useState("All LGAs");
  const [selectedState, setSelectedState] = useState(stateName);
  const [lgaList, setLgaList] = useState([]);

  useEffect(() => {
    if (data && selectedState) {
        const newLgaList = data.allServiceProvidersCsv.nodes
                              .filter(node => node.state === selectedState)
                              .map(node => node.lga_name)
                              .filter((value, index, self) => self.indexOf(value) === index);
        setLgaList(newLgaList);
    }
}, [data, selectedState]);
  // Assuming selectedState and setSelectedState are defined elsewhere in your component
  // If not, you need to define them here as well using useState.

  const handleServiceChange = (service) => {
    setSelectedService(service);
  };

  const handlePopulationChange = (population) => {
    setSelectedPopulation(population);
  };

  const handleLocationChange = (location) => {
    setSelectedLocation(location);
  };

  const getTitle = () => {
    if (selectedService !== services[0]) {
      return `Number of Providers for ${selectedService}`;
    }
    if (selectedPopulation !== populations[0]) {
      return `Number of Providers for ${selectedPopulation}`;
    }
    if (selectedLocation !== locations[0]) {
      return `Number of Providers in ${selectedLocation}`;
    }
    return 'Number of HIV Prevention Service Providers';
  }


  return (
    <Layout>
      <div className='holder'>
        <Sidebar
          selectedState={selectedState} // Ensure selectedState is defined
          setSelectedState={setSelectedState} // Ensure setSelectedState is defined
          selectedLga={selectedLga} // Ensure selectedState is defined
          setSelectedLga={setSelectedLga} 
          selectedService={selectedService}
          setSelectedService={setSelectedService}
          selectedPopulation={selectedPopulation}
          setSelectedPopulation={setSelectedPopulation}
          selectedLocation={selectedLocation}
          setSelectedLocation={setSelectedLocation}
          onServiceChange={handleServiceChange}
          onPopulationChange={handlePopulationChange}
          onLocationChange={handleLocationChange}
          lgaList={lgaList}
        />

        <div className="map-header-container">
          <div id='current-heading-container'>
          </div>
          <div className="current-heading">{currentHeading}</div>
          <h4 className="map-title"> {getTitle()} </h4>

          <StateMap
            stateName={stateName}
            setCurrentHeading={setCurrentHeading}
            selectedService={selectedService}
            selectedPopulation={selectedPopulation}
            selectedLocation={selectedLocation}
          />
        </div>
      </div>
    </Layout>
  );
};

export default StateTemplate;