import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';
import { navigate } from 'gatsby';
import "../styles/index.css";

function getStateGeoJSONPath(stateName) {
    if (!stateName) {
        console.warn("getStateGeoJSONPath was called with an undefined or null stateName.");
        return '';
    }
    return `/mhps2023/${stateName.replace(/\s+/g, '_').toLowerCase()}.geojson`;

}

const StateMap = ({ stateName, setCurrentHeading, selectedService, selectedPopulation, selectedLocation }) => {
    const svgRef = useRef(null);
    const offsetY = 150;
    const [tooltipVisible, setTooltipVisible] = useState(false);
    const [tooltipType, setTooltipType] = useState('map');
    const [tooltipContent, setTooltipContent] = useState("");
    const [tooltipPosition, setTooltipPosition] = useState({ x: 0, y: 0 });

    const serviceToColumnMap = {
        "All Services": "serviceProviders",
        "Condom Distribution": "CondomDistribution",
        "Family Life and HIV/AIDS Education (FLHE)/Sexuality Education": "FamilyLifeAndHivAidsEducationFlheSexualityEducation",
        "Gender and Human Rights (GHR)": "GenderAndHumanRightsGhr",
        "Harm Reduction: Medication-Assisted Treatment (MAT)": "HarmReductionMedicationAssistedTreatmentMat",
        "Harm Reduction: Needle and Syringe Exchange": "HarmReductionNeedleAndSyringeExchange",
        "HIV Self-Testing (HIVST)": "HivSelfTestingHivst",
        "HIV Testing Services (HTS)": "HivTestingServicesHts",
        "Mental Health Services": "MentalHealthServices",
        "Pre-Exposure Prophylaxis (PrEP)": "PreExposureProphylaxisPrep",
        "Social and Behaviour Change Communication (SBCC)": "SocialAndBehaviourChangeCommunicationSbcc",
        "STI Screening and Treatment": "StiScreeningAndTreatment",
        "Technical Assistance (TA)": "TechnicalAssistanceTa"
    };

    const populationToColumnMap = {
        "All Populations": "allPopulations",
        "Adolescent Girls and Young Women (AGYW) in school": "agywInSchool",
        "Adolescent Girls and Young Women (AGYW) out of school": "agywOutOfSchool",
        "Adolescent Boys and Young Men (ABYM) in school": "abymInSchool",
        "Adolescent Boys and Young Men (ABYM) out of school": "abymOutOfSchool",
        "Children living with HIV": "childrenLivingWithHIV",
        "General population": "generalPopulation",
        "HIV-exposed infants": "hivExposedInfants",
        "Internally Displaced People (IDP)": "idp",
        "Key Populations - Female Sex Workers (FSW)": "kpFsw",
        "Key Populations - Men who have Sex with Men (MSM)": "kpMsm",
        "Key Populations - People in enclosed settings": "kpPeopleInEnclosedSettings",
        "Key Populations - People Who Inject Drugs (PWID)": "kpPwid",
        "Key Populations - Transgender": "kpTransgender",
        "Orphans and Vulnerable Children (OVC)": "ovc",
        "Parents": "parents",
        "People living with disabilities": "peopleLivingWithDisabilities",
        "People Living with HIV (PLHIV)": "plhiv",
        "Serodiscordant couples": "serodiscordantCouples",
        "Teachers": "teachers",
        "Faith Leaders": "faithLeaders",
        "Fishermen": "fishermen",
        "Pastoralists": "pastoralists",
        "Transport Workers": "transportWorkers"
    };

    const locationToColumnMap = {
        "All Locations": "allLocations",
        "Community": "community",
        "Facility": "facility",
        "School": "school"
    }

    const stateCodeToNameMap = {
        "AB": "Abia",
        "AD": "Adamawa",
        "AK": "Akwa Ibom",
        "AN": "Anambra",
        "BA": "Bauchi",
        "BY": "Bayelsa",
        "BE": "Benue",
        "BR": "Borno",
        "CR": "Cross River",
        "DE": "Delta",
        "EB": "Ebonyi",
        "ED": "Edo",
        "EK": "Ekiti",
        "EN": "Enugu",
        "FC": "Federal Capital Territory",
        "GO": "Gombe",
        "IM": "Imo",
        "JI": "Jigawa",
        "KD": "Kaduna",
        "KN": "Kano",
        "KT": "Katsina",
        "KB": "Kebbi",
        "KO": "Kogi",
        "KW": "Kwara",
        "LA": "Lagos",
        "NA": "Nasarawa",
        "NI": "Niger",
        "OG": "Ogun",
        "ON": "Ondo",
        "OS": "Osun",
        "OY": "Oyo",
        "PL": "Plateau",
        "RI": "Rivers",
        "SO": "Sokoto",
        "TA": "Taraba",
        "YO": "Yobe",
        "ZA": "Zamfara"
    };

    const stateGeoJSONPath = getStateGeoJSONPath(stateName);

    useEffect(() => {
        const svg = d3.select(svgRef.current);
        const width = 930;
        const height = 930;
        const projection = d3.geoMercator().translate([width / 3, (height / 2) - offsetY]);
        const path = d3.geoPath().projection(projection);
        const colorScale = d3.scaleThreshold()
            .domain([0, 1, 6, 11, 21, 31])
            .range(["#D3D3D3", "#f7faf5", "#a0d492", "#78b971", "#60a455", "#3e8d00", "#008000"]);

        const updateVisualization = async () => {
            try {
                const lgaGeoData = await d3.json(stateGeoJSONPath);
                // Adjust projection based on geoData
                const bounds = path.bounds(lgaGeoData);
                const centroid = d3.geoCentroid(lgaGeoData);
                const dx = bounds[1][0] - bounds[0][0];
                const dy = bounds[1][1] - bounds[0][1];
                const scale = 0.065 / Math.max(dx / width, dy / height) * width;
                projection.scale(scale).center(centroid);


                Promise.all([
                    d3.csv("/mhps2023/service_providers.csv")
                ]).then(([serviceData]) => {
                    // Define stateDataLookup here
                    const lgaDataLookup = serviceData.reduce((acc, row) => {
                        acc[row.lga_name] = row;
                        return acc;
                    }, {});

                    // Determine the aggregation column based on the selected filters
                    let aggregationColumn = 'serviceProviders'; // Default column

                    if (selectedService !== 'All Services') {
                        aggregationColumn = serviceToColumnMap[selectedService];
                    } else if (selectedPopulation !== 'All Populations') {
                        aggregationColumn = populationToColumnMap[selectedPopulation];
                    } else if (selectedLocation !== 'All Locations') {
                        aggregationColumn = locationToColumnMap[selectedLocation];
                    }

                    // Create a map for count by LGA based on the selected filter
                    const countByLGA = serviceData.reduce((acc, row) => {
                        const lgaName = row.lga_name;
                        const count = +row[aggregationColumn] || 0;
                        acc[lgaName] = (acc[lgaName] || 0) + count;
                        return acc;
                    }, {});

                    svg.selectAll("path").remove();
                    svg.append("g")
                        .selectAll("path")
                        .data(lgaGeoData.features)
                        .enter().append("path")
                        .attr("d", path)
                        .attr("fill", d => {
                            const count = countByLGA[d.properties.lga_name]; // Replace 'NAME' with the property name for LGA in your GeoJSON
                            return count != null ? colorScale(count) : "#f7faf5"; // Use color scale if count exists, otherwise use default color
                        })
                        .attr("stroke", "#54595f") // Black borders for states
                        .attr("stroke-width", 3)
                        .on("mouseover", function (event, d) {
                            const lgaNamed = d.properties.lga_name; // Assuming the state name is in the 'name' property
                            // Use the state name to get the current value from the stateDataLookup
                            const lgaInfo = lgaDataLookup[lgaNamed];
                            // Use aggregationColumn to dynamically display the data
                            const currentValue = lgaInfo && lgaInfo[aggregationColumn] ? lgaInfo[aggregationColumn] : 'No data';
                            setCurrentHeading(lgaNamed); // Update state name display
                            setTooltipContent(`${lgaNamed}: ${currentValue}`);
                            setTooltipType('map');
                            setTooltipVisible(true);
                            setTooltipPosition({ x: event.pageX, y: event.pageY });
                            d3.select(this).attr("fill", "#eb4747");
                        })
                        .on("mouseout", function (event, d) {
                            setCurrentHeading(stateName);
                            setTooltipVisible(false);
                            // Retrieve the count again for the current LGA
                            const count = countByLGA[d.properties.lga_name];
                            // Set the fill color based on the count, just like when the paths were initially created
                            d3.select(this).attr("fill", count != null ? colorScale(count) : "#f7faf5");
                        })
                        
                        .on("click", function (event, d) {
                            const lgaURL = `/mhps2023/${d.properties.lga_name.toLowerCase().replace(/\s+/g, '-')}/`;
                            navigate(lgaURL);
                        });
                        


                    const legendWidth = 30;
                    const legendHeight = 20;

                    // Calculate translation for legend to be positioned bottom-right
                    const translateX = 650;  // 800 - 100 (legend total width with some padding)
                    const translateY = 400;  // 800 - 200 (considering 5 blocks of color and some padding)

                    // Create a group for the legend
                    const legend = svg.append("g")
                        .attr("transform", `translate(${translateX},${translateY})`);

                    let legendData = colorScale.domain().map(lowerBound => {
                        return [lowerBound, colorScale.invertExtent(colorScale(lowerBound))[1]];
                    });

                    // Filter out duplicates, based on the first element of each tuple
                    legendData = legendData.filter((value, index, self) =>
                        self.findIndex(v => v[0] === value[0]) === index);

                    // Add the "No data" category at the start
                    if (!legendData.some(arr => arr[0] === 0)) {
                        legendData.unshift([0, 0]);
                    }


                    const legendGroups = legend.selectAll("g")
                        .data(legendData)
                        .enter().append("g")
                        .attr("transform", (d, i) => `translate(0, ${i * legendHeight})`);

                    legendGroups.append("rect")
                        .attr("width", legendWidth)
                        .attr("height", legendHeight)
                        .attr("fill", d => {
                            // Log to debug color assignment for legend
                            console.log('Legend color for range', d, 'is', colorScale(d[0]));
                            return colorScale(d[0]);
                        });

                    legendGroups.append("text")
                        .attr("x", legendWidth + 5)
                        .attr("y", legendHeight / 2)
                        .attr("dy", "0.35em")
                        .attr("font-size", "12px")
                        .attr("font-family", "'Lato', sans-serif")
                        .text(d => {
                            // Log to debug text assignment for legend
                            console.log('Legend text for range', d, 'is', d[0] === 0 ? `0` : `${d[0]} - ${d[1] - 1}`);
                            if (d[0] === 0) return `0`;
                            if (d[1] === undefined) return 'Above 30'; // Adjust this if your data has a different upper bound
                            return `${d[0]} - ${d[1] - 1}`;
                        });

                })


            } catch (error) {
                console.error("Error loading file:", error);
            }
        };

        updateVisualization();
    }, [selectedService, selectedPopulation, selectedLocation]);

    return (
        <div>
            <div className="map-container state">
                <svg ref={svgRef} width="800" height="800" />

                {tooltipVisible && (
                    <div
                    className="map-tooltip"
                    style={{
                        position: 'absolute',
                        top: tooltipPosition.y,
                        left: tooltipPosition.x
                    }}
                >
                        {tooltipContent}
                    </div>
                )}
            </div>
        </div>

    )
};


export default StateMap;
